import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { config } from "@fortawesome/fontawesome-svg-core"
import { faDownload } from "@fortawesome/free-solid-svg-icons"
import React from "react"
import "../styles/_aboutMe.scss"
import ImagePf from "./imagePf"
config.autoAddCss = false

const AboutMe = () => (
  <>
    <h1 className="titleTwo">About Me</h1>
    <div className="caja">
      <div className="resume">
        <h2>PROFILE</h2>
        <p className="textProfile">
          University Programmer Analyst, highly responsible and punctual
          professional, willing to learn and adapt to any work environment, with
          good management of interpersonal relationships and interest in
          professional updating to solve problems efficiently and achieve
          proposed goals.
        </p>
        <button>
          <span className="download">
            Download CV{' '}
            <FontAwesomeIcon icon={faDownload} />
          </span>
        </button>
      </div>
      <div>
        <ImagePf></ImagePf>
      </div>
      <div className="skills">
        <h2>SKILLS</h2>
        <ul>
          <li>
            <p>
              <h5>Gatsby</h5>
              Plugins, Flex, FontAwesomeIcon, Deploy
              in Github Pages. Backend: Node.js in Azure App Service  
            </p>
          </li>
          <li>
            <p>
              <h5>C#</h5>
              OOP. SQL. Entity Framework
            </p>
          </li><li>
            <p>
              <h5>Support</h5>
              Problem resolution. Responsable. Proactive. Adaptable
            </p>
          </li><li>
            <p>
              <h5>Angular</h5>Components. Angular Material. Flex-Layout
            </p>
          </li>
        </ul>
      </div>
    </div>
  </>
)

export default AboutMe
